
.feedback{
    // outline: 3px solid red;
    padding-top: 80px;
    padding-bottom: 86px;
    @media screen and (min-width:768px){
        padding-top: 121px;
        padding-bottom: 166px;
    }
    @media screen and (min-width:1200px){
        padding-top: 123px;
        padding-bottom: 132px;
    }
}


.slide{
    box-sizing: border-box;
}


.client-photo{
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 29px;
    @media screen and (min-width:768px)
    {
        margin-bottom: 19px;
    }
    @media screen and (min-width:1200px)
    {
        margin-bottom: 15px;
    }
}

.text-area{
    margin-left: auto;
    margin-right: auto; 
}

.review{
    @media screen and (max-width:767px){
     padding-top: 43px;
    padding-bottom: 26px;
    padding-left: 63px;
    padding-right: 63px;
    margin-bottom: 30px;
    max-width: 157px;
    height: 239px; 
    }
   
    @media screen and (min-width:768px){
        max-width: 460px;
        height: 83px;
        padding-left: 80px;
        padding-right: 80px;
        padding-top: 47px;
        padding-bottom: 34px;
        background-position: 40px 30px;
    }

    @media screen and (min-width:1200px){
        max-width: 750px;
        height: 52px;
        padding-left: 88px;
        padding-right: 88px;
        padding-top: 47px;
        padding-bottom: 39px;
        background-position: 50px 32px;
    }
    font-family: DM Sans;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 255%;
    margin: auto;
    // border: 1px solid green;
    background-color: #FAFAFA;
    background-image:url('../images/review/background-review.png');
    background-repeat: no-repeat;
    background-position: 61px 28px;

}



.client{
    font-family: DM Sans;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 186%;
    text-align: center;
    
}

.decoration{
    display: flex;
    justify-content: space-between;
    
    width: 24px;
    
    margin-left: auto;
    margin-right: auto;
    margin-top: 30px;
    margin-bottom: 18px;

    @media screen and (min-width:768px){
        margin-top: 24px;
        margin-bottom: 14px;}

    @media screen and (min-width:1200px){
        margin-top: 15px;
        margin-bottom: 15px;
    }

}